.chat-sanders {
    .card-header {
        background: linear-gradient(-135deg, #3f4d67 0%, #3f4d67 100%);
    }

    .msg {
        margin-bottom: 5px;
        display: inline-block;
        padding: 15px;
        position: relative;
    }

    .scroll-wrapper {
        height: 305px;
    }

    .received-chat {
        h5 {
            border-radius: 50%;
            width: 40px;
            height: 40px;
            text-align: center;
        }

        .msg {
            background: #fff;
            box-shadow: 0 5px 10px 0 rgba(0, 0, 0, 0.1);

            &:after {
                content: "";
                position: absolute;
                left: -12px;
                bottom: 18px;
                transform: rotate(270deg);
                border: 6px solid transparent;
                border-bottom-color: #fff;
            }
        }
    }

    .send-chat {
        h5 {
            border-radius: 50%;
            width: 40px;
            height: 40px;
            text-align: center;
        }

        .msg {
            background: linear-gradient(-135deg, #1de9b6 0%, #1dc4e9 100%);
            box-shadow: 0 5px 10px 0 rgba(0, 0, 0, 0.1);

            &:after {
                content: "";
                position: absolute;
                right: -11px;
                bottom: 18px;
                transform: rotate(450deg);
                border: 6px solid transparent;
                border-bottom-color: #1de9b6;
            }
        }
    }

    .btn {
        background: none;
        opacity: 0.4;
    }

    .form-control {
        background: #f4f7fa;
    }

    .input-group {
        background: transparent;
    }
}

