/* ===================== to do page ============================= */

#task-container {
    ul {
        overflow: hidden;
        padding: 0;
    }

    li {
        float: left;
        width: 49%;
        overflow: auto;
        height: auto;
        min-height: 10px;
        background: #fff;
        display: inline-block;
        padding: 20px;
        border: 1px solid #ccc;
        color: #666;
        border-top: 5px solid $primary-color;
        cursor: pointer;
        margin-bottom: 20px;
        margin-right: 2%;
        transition: all 0.3s;
        position: relative;

        &:nth-child(2n) {
            margin-right: 0;
        }
    }

    li.complete {
        opacity: 1;
        border-top: 9px solid $danger-color;
        transition: all ease-in 0.3s;

        &:before {
            background: url("../images/complete.png") no-repeat;
            position: absolute;
            top: 5px;
            right: 5px;
            content: "";
            width: 55px;
            height: 55px;
            background-size: 100%;
        }

        p {
            text-decoration: line-through;
        }
    }
    @media screen and (max-width:580px) {
        li {
            width: 100%;
        }
    }
}

.new-task {
    .to-do-list {
        .cr {
            float: left;
            top: 0;
            margin-right: 16px;
        }
    }

    label input[type=checkbox] {
        display: none;
    }
}

.checkbox-fade {
    .cr {
        border-radius: 6px;
        border: 1px solid #e3e3e3;
        cursor: pointer;
        display: inline-block;
        float: left;
        height: 24px;
        margin-right: 0.5em;
        position: relative;
        width: 24px;
        margin-right: 16px;
        top: 0;
    }
}

.task-panel {
    .to-do-label {
        margin-bottom: 15px;

        &:last-child {
            border-bottom: none;
            margin-bottom: 0;
            padding: 0;
        }
    }
}

.done-task .captions,
.done-task span {
    color: #919aa3;
    text-decoration: line-through;
}

.to-do-list {
    i {
        color: #ccc;
        font-size: 17px;
        opacity: 0;
    }

    &:hover {
        i {
            opacity: 1;
            transition: opacity ease-in 0.3s;
        }
    }
}

.note-card {
    .note-box-aside {
        border-right: 1px solid #ddd;
    }

    .Note-header {
        padding: 20px 0;
    }

    #Note-pad {
        border: none;
        resize: none;
        background: 0 0;
        padding: 0 20px 0 50px;
        line-height: 35px;
    }

    .note-write {
        position: relative;
        background: -webkit-linear-gradient(to top,#98dcfa 0%,#e1f5fe 5%) 0 0;
        background-size: 100% 35px;

        &:before {
            content: '';
            position: absolute;
            width: 0;
            top: 0;
            left: 32px;
            bottom: 0;
            border-left: 1px solid #4fc3f7;
        }

        &:after {
            content: '';
            position: absolute;
            width: 0;
            top: 0;
            left: 34px;
            bottom: 0;
            border-left: 1px solid #4fc3f7;
        }
    }

    .Note-delete {
        margin-bottom: 0;
        opacity: 0;
        background: rgba(0,0,0,.6);
        border: none;
        position: absolute;
        right: 20px;
        color: #fff;
        transition: all ease-in 0.3s;
        border-radius: 2px;
        cursor: pointer;
        transform: translateX(10px);
    }

    #Note-list {
        li:hover {
            .Note-delete {
                opacity: 1;
                transform: translateX(0px);
            }
        }
    }
}
