.syntax-output {
    border-radius: 0.25rem;
    border: 1px solid $theme-border;
}

.owl-carousel {
    .owl-nav {
        button {
            &.owl-next,
            &.owl-prev {
                padding: 3px 9px !important;
                background-color: $primary-color;
                color:#fff;
            }
        }
    }
    button:focus{
        outline:none;
        color:#fff;
    }
}
.owl-theme .owl-nav [class*=owl-]:hover{
    background-color: $primary-color;
    color:#fff;
}

.counter b {
    font-size: 24px;
}

.tour-mobile .error-block {
    display: none;
}

.message-mobile {
    span {
        font-size: 16px;
    }

    .task-right-header-status {
        margin-bottom: 10px;
        padding: 10px 0;
        border-bottom: 1px solid #f1f1f1;
        cursor: pointer;
        @media screen and (min-width: 992px) {
            display: none;
        }
    }
}

.fullcalendar-card {
    .fc-button {
        background-color: $primary-color;
        border-color: #fff;
        color: #fff;
        background-image: none;
        box-shadow: none;
        text-shadow: none;
        height: 37px;
        padding: 0 15px;
    }

    h2 {
        font-size: 28px;
    }
    @media screen and (max-width:768px) {
        .fc .fc-toolbar>*>*{
            float:none;
        }
        .fc-toolbar .fc-left,.fc-toolbar .fc-right,.fc-toolbar .fc-center{
            float:none;
            display:flex;
            justify-content: center;
            align-items: center;
            width:100%;
            text-align: center;
            margin: 0 auto;
            margin-bottom:10px;
        }
    }
}

.ck-content strong {
    font-weight: 600;
}

div.code-toolbar{
    > .toolbar a, > .toolbar button, >.toolbar span{
        padding: 3px 0.9em !important;
        background:#04a9f5 !important;
        color:#fff !important;
        box-shadow:none !important;
    }
}

pre[class*=language-]:after, pre[class*=language-]:before{
    display: none;
}
#chat-scroll{
    height: 280px;
    position: relative;
}
.msg-user-list.scroll-div{
    height:calc(100vh - 300px);
    position: relative;
}
.msg-user-chat.scroll-div{
    height:calc(100vh - 330px);
    position: relative;
}

@media screen and (max-width: 1199px) {
    .note-card .note-box-aside{
        border:none !important;
    }
}

@media screen and (max-width: 991px) {
    .ch-block {
        display: none;
    }

    .msg-block {
        &.dis-chat {
            .taskboard-right-progress {
                display: none;
            }

            .ch-block {
                display: block;
            }
        }
    }
}
@media screen and (max-width: 992px) {
    .tour-mobile .error-block {
        display: block;
    }

    .tour-mobile .page-wrapper {
        display: none;
    }
}
@media screen and (min-width: 768px) {
    .horizontal-mobile {
        display: none;
    }
}
